<template>
  <div class="marquee">
    <div class="marquee_box">
      <ul class="marquee_list" :class="{ marquee_top: animate }">
        <li v-for="(item, index) in list" :key="index">
          <span
            >恭喜{{ item.nickName }}成功邀请38合力，获得{{ item.prize }}!</span
          >
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      animate: false,
      list: [
        {
          nickName: "Angel",
          prize: "萃雅莹白精华乳",
        },
        {
          nickName: "Angel2",
          prize: "萃雅莹白精华乳",
        },
        {
          nickName: "Angel3",
          prize: "萃雅莹白精华乳",
        },
        {
          nickName: "Angel4",
          prize: "萃雅莹白精华乳",
        },
      ],
    };
  },
  created: function () {
    setInterval(this.showMarquee, 2000);
  },
  methods: {
    showMarquee: function () {
      this.animate = true;
      setTimeout(() => {
        this.list.push(this.list[0]);
        this.list.shift();
        this.animate = false;
      }, 500);
    },
  },
};
</script>
<style lang="scss" scoped>
.marquee_top {
  transition: all 0.5s;
  margin-top: -24px;
}

.marquee {
  width: 100%;
  height: 50px;
  align-items: center;
  color: #3a3a3a;
  display: flex;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 10;
  box-sizing: border-box;
}
.marquee_box {
  display: block;
  position: relative;
  width: 90%;
  margin: 0 auto;
  height: 24px;
  overflow: hidden;
  border: 2px #cee2f1 solid;
  border-radius: 8px;
}

.marquee_list {
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.marquee_list li {
  width: 100%;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  color: #646a78;
  background-color: #e9f0f5;
}

.marquee_list li span {
  padding: 0 2px;
}
</style>
